<template>
	<div class="usersearch">
		<!-- <div class="div_input">
			<el-input v-model="searchcontent" :placeholder="searchholder" clearable @clear="clear"></el-input>
		</div>
		<div>
			<el-button type="primary" @click="search" icon="el-icon-search" size="mini">{{$t('common.search')}}</el-button>	
		</div> -->
		<div class="adduser">
			<el-button type="success" @click="addUser" size="mini" :disabled="permissionFlag">{{$t('common.add')}}</el-button>
		</div>
	</div>
</template>
<script>
import { permission } from '@/utils/public'
	export default {
		props:['searchholder'],
		data () {
			return {
				searchcontent:'',
				permissionFlag:permission(),
		
			}
		},
		methods: {
			clear () {

			},
			search () {

			},
			addUser () {
				this.$emit('listenAdd')
			}
		}		
	}
</script>
<style scoped>
	.usersearch{
		height: 40px;
		line-height: 40px;
		display: flex;
		font-size: 14px;
		margin:10px 0;
	}
	.adduser{
		margin:0 20px;
	}
	.adduser .el-button{
		height: 40px;
	}
</style>


