<template>
	<div>
		<v-usersearch
      :searchholder="searchholder"
      v-on:listenAdd="Add">
    </v-usersearch>
    <v-useredit ref="edit" v-on:getUserList="getUserList"></v-useredit>
    <v-adduser ref="add" v-on:getUserList="getUserList"></v-adduser>
		<template>
			<el-table
				:header-cell-style="{background:'#DFEBF9',color:'#606266'}"
				:loading="loading"
				:data="user_list"
        :empty-text="$t('common.nodata')"
				border>									
				<el-table-column
          :label="$t('common.No')"
          type="index"
          width="50">
        </el-table-column>
				<el-table-column
					prop="user_name"
				  	:label="$t('user.username')">						
				</el-table-column>
				<el-table-column
					prop="group_name"
				  	:label="$t('user.authorization')">						
				</el-table-column>
				<el-table-column
				  	prop=""
				  	:label="$t('common.operation')"
				  	width="158">
				  	<template slot-scope="scope">
              <el-button type='warning' size="mini"  @click="editUser(scope.row)" :disabled="permissionFlag">{{$t('common.edit')}}</el-button>
				  		<el-button type='danger' size="mini"  @click="deleteUser(scope.row.user_id)" :disabled="permissionFlag?true:false">{{$t('common.delete')}}</el-button>
				  	</template>				 
				</el-table-column>
			</el-table>
		</template>
		<!-- <v-pagination :totalNum="totalNum"></v-pagination> -->
	</div>
</template>
<script>
import VUsersearch from '@/components/user/commonuser/usersearch'
// import VPagination from '@/components/common/pagination'
import VUseredit from '@/components/user/commonuser/useredit'
import VAdduser from '@/components/user/commonuser/adduser'
import { permission } from '@/utils/public'
import { userList, delUser } from '@/request/api/user';// 导入api接口
export default {
	components:{ VUsersearch, VUseredit, VAdduser},
	data () {
		return {
			loading:true,
			permissionFlag:permission(),
			searchholder:this.$t('user.searchholder'),
			user_list:[],
      grouplist:[],
      userid:'',
		}
	},
  created(){

  },
  mounted(){
    this.getUserList(); 
  },
	methods:{
    // 获取用户列表
    getUserList () {
      userList({warehouse_category_id:0}).then(res =>{
        this.loading = false
          if(res.result){
            this.user_list = res.user_list
          }else{
            this.$message({
              showClose: true,
              message: res.msg,
              type: 'error'
            })
          }
      })
    },
    Add () {
      this.$refs.add.open()
    },
    editUser (userInfo) {
      this.$nextTick(()=> {        
        this.$refs.edit.userDetail(userInfo)
      })
    },
		deleteUser (id) {
      this.$confirm(this.$t('user.deltips'), this.$t('user.tips'), {
        confirmButtonText: this.$t('common.sure'),
        cancelButtonText: this.$t('common.cancel'),
        type: 'warning'
      }).then(() => {
        delUser({del_user_id:id}).then(res =>{
          if (res.result) {
            this.getUserList()
          }
        })
      }).catch(() => {
        // this.$message({
        //   type: 'info',
        //   message: '已取消删除'
        // });          
      });
		},
    
	}
}
</script>



