<template>
<div>
	<el-dialog
    :title="$t('user.adduser')"
    :visible.sync="addVisible"
    @close="reset('userform')"
    width="760px">
		<div class="add_user_form">
			<el-form ref="userform" :inline="true" :model="userform" :rules="rules" label-width="100px">
			  <el-form-item :label="$t('user.username')" prop="add_user_name">
			    <el-input v-model="userform.add_user_name"></el-input>
			  </el-form-item>			  
			  <el-form-item :label="$t('user.authorization')" prop="add_user_group_id">
			    <el-select v-model="userform.add_user_group_id" placeholder="" value-key="id">
			      <el-option
              v-for="item in grouplist"
              :key="item.id"
              :label="item.group_name"
              :value="item.id">                         
            </el-option>
			    </el-select>
			  </el-form-item>
			   <el-form-item :label="$t('storage.category_name')" >
			   	<el-select value-key="id" v-model="userform.add_warehouse_category_id" placeholder="请选择库位类别" @change="categoryTypeChange" :disabled="userform.add_user_group_id<=2?true:false ">
				    	<el-option
					      	v-for="item in categoryTypeList"
					      	:key="item.id"
					      	:label="item.category_name"
					      	:value="item.id">										      
				    	</el-option>
	    			</el-select>
			   </el-form-item>
			   <el-form-item :label="$t('storage.warehouse_name')" >
			   	<el-select value-key="id" v-model="userform.add_warehouse_name_id" placeholder="请选择库位类别" :disabled="userform.add_user_group_id<=3?true:false ">
				    	<el-option
					      	v-for="item in warehouseList"
					      	:key="item.id"
					      	:label="item.warehouse_name"
					      	:value="item.id">										      
				    	</el-option>
	    			</el-select>
			   </el-form-item>
			  <el-form-item :label="$t('user.password')" prop="add_user_passwd" class="form_item_left">
			    <el-input v-model="userform.add_user_passwd" show-password auto-complete="new-password"></el-input>
			  </el-form-item><br>
			  <div style="text-align:center">
			  	<el-button @click="userCancel('userform')">{{$t('common.cancel')}}</el-button>
			  	<el-button type="primary" @click="userSave('userForm')">{{$t('common.save')}}</el-button>
			  </div>
			  
	  	</el-form>
	</div>
	</el-dialog>
</div>
</template>
<script>
	import { userGroupList, addUser } from '@/request/api/user';// 导入api接口
	import { searchWarehouseCategory, searchWarehouseNameByCategory } from '@/request/api/materialshelf';// 导入api接口
	export default {
		data() {
			return {
				addVisible:false,
				grouplist:[],
				categoryTypeList:[{'category_name':'所有','id':0}],
				warehouseList:[{'warehouse_name':'所有','id':0}],
				userform:{
					add_user_group_id: null,
					add_user_passwd:"",
					add_user_name: "",
					add_warehouse_category_id:0,
					add_warehouse_name_id:0,
				},
				rules:{
					add_user_name:[
						{ required: true, message: this.$t('user.userholder'), trigger: 'blur' },
						{
			        validator: (rule, value, callback) => {
			          if (/[\u4e00-\u9fa5_a-zA-Z0-9_]{2,16}/.test(value)==false) {
			            callback(new Error(this.$t('user.usertips')));
			          }else{
			          	callback();
			          }
			        }, trigger: 'blur'
				    },
					],
					add_user_passwd:[{ required: true, message: this.$t('user.passwordholder'), trigger: 'blur' }],
					add_user_group_id:[{ required: true, message: this.$t('user.authorizationholder'), trigger: 'change' }],
				}				
			}
		},
		watch:{
			"userform.add_user_group_id":{
				deep:true,
				handler: function(newV, oldV) {
					if (newV==2) {
						this.userform.add_warehouse_category_id = 0
						this.userform.add_warehouse_name_id = 0
					}
					if (newV==3) {
						this.userform.add_warehouse_name_id = 0
					}
				}
			}
		},
		mounted(){
			 this.userform.user_passwd = ''
			 this.userform.add_user_name = ''
		},
		methods: {
			 //获取权限组列表
    	getUserGroupList(){
	      userGroupList({}).then(res =>{
          if(res.result){
            this.grouplist = res.data
          }else{
            this.$message({
              showClose: true,
              message: res.msg,
              type: 'error'
            })
          }
	      })
	    },
	    // 获取仓库类型
	    getCategoryType(){
	    	searchWarehouseCategory({}).then(res =>{
	    		if (res.result) {
	    			this.categoryTypeList = this.categoryTypeList.concat(res.data_list)
	    		}
	    	})
	    },
	    // 获取库位
	    getWarehouseList(){
	    	this.warehouseList = [{'warehouse_name':'所有','id':0}]
	    	this.userform.add_warehouse_name_id = 0
	    	searchWarehouseNameByCategory({warehouse_category_id:this.userform.add_warehouse_category_id}).then(res =>{
	    		if (res.result) {
	    			this.warehouseList = this.warehouseList.concat(res.data)
	    		}
	    	})
	    },
	    // 获取库位
	    categoryTypeChange(){
	    	this.userform.add_warehouse_name_id = 0
	    	this.getWarehouseList()
	    },
			open(){
				this.getUserGroupList(); 
				this.getCategoryType();
				this.userform.add_user_group_id = "";
				this.userform.add_user_passwd = "";
				this.userform.add_user_name = "";
				this.userform.add_warehouse_category_id = 0;
				this.userform.add_warehouse_name_id = 0;
				this.addVisible = true;
			},
			reset(userform){
       this.$refs.userform.resetFields();
    	},
			// 保存用户
			userSave() {
				let newV = this.userform.add_user_group_id
				if (newV==2) {
						this.userform.add_warehouse_category_id = 0
						this.userform.add_warehouse_name_id = 0
					}
					if (newV==3) {
						this.userform.add_warehouse_name_id = 0
					}
				this.$refs.userform.validate((valid) => {
          if (valid) {
            addUser(this.userform).then(res=>{
            	if (res.result) {
            		this.addVisible = false
            		this.$emit('getUserList')
            	}
            })
          } else {
            return false;
          }
        });
			},
			// 取消保存
			userCancel(userform) {
				 this.$refs.userform.resetFields();
				 this.addVisible = false;
			}
		}
	}
</script>
<style>
.add_user_form{
	text-align: left;
}
.add_user_form .el-input__inner{
	width:220px;
}
.form_item_left{
	text-align: left!important;
}
/*.add_user_form {
	margin:0 20px;
}*/
	.small_form .el-form-item__label{
	text-align: left;
	}
	.small_form .el-form-item__content{
		width:200px;
	}
	.small_form .el-input .el-input__inner{
		width:200px;
	}
</style>


