<template>
<div>
	<el-dialog
    :title="$t('user.edituser')"
    :visible.sync="editVisible"
    @close="reset('userform')"
    width="760px">
		<div class="add_user_form">
			<el-form ref="userform" :inline="true" :model="userform" :rules="rules" label-width="100px">
			  <el-form-item :label="$t('user.username')" prop="target_user_name">
			    <el-input v-model="userform.target_user_name" disabled></el-input>
			  </el-form-item>			  
			  <el-form-item :label="$t('user.authorization')" prop="user_group_id">
			    <el-select v-model="userform.user_group_id" placeholder="" value-key="id">
			      <el-option
              v-for="item in grouplist"
              :key="item.id"
              :label="item.group_name"
              :value="item.id">                         
            </el-option>
			    </el-select>
			  </el-form-item>
			   <el-form-item :label="$t('storage.category_name')" >
			   	<el-select value-key="id" v-model="userform.warehouse_category_id" placeholder="请选择库位类别" @change="categoryTypeChange" :disabled="userform.user_group_id<=2?true:false ">
				    	<el-option
					      	v-for="item in categoryTypeList"
					      	:key="item.id"
					      	:label="item.category_name"
					      	:value="item.id">										      
				    	</el-option>
	    			</el-select>
			   </el-form-item>
			   <el-form-item :label="$t('storage.warehouse_name')" >
			   	<el-select value-key="id" v-model="userform.warehouse_name_id" placeholder="请选择库位类别" :disabled="userform.user_group_id<=3?true:false ">
				    	<el-option
					      	v-for="item in warehouseList"
					      	:key="item.id"
					      	:label="item.warehouse_name"
					      	:value="item.id">										      
				    	</el-option>
	    			</el-select>
			   </el-form-item>
			  <el-form-item :label="$t('user.password')"  class="form_item_left">
			    <el-input v-model="userform.user_passwd" show-password auto-complete="new-password"></el-input>
			  </el-form-item><br>
			  <div style="text-align:center">
			  	<el-button @click="userCancel('userform')">{{$t('common.cancel')}}</el-button>
			  	<el-button type="primary" @click="userSave('userForm')">{{$t('common.save')}}</el-button>
			  </div>
			  
	  	</el-form>
	</div>
	</el-dialog>
</div>
</template>
<script>
	import { userGroupList, addUser, adminChangeUserInfo } from '@/request/api/user';// 导入api接口
	import { searchWarehouseCategory, searchWarehouseNameByCategory } from '@/request/api/materialshelf';// 导入api接口
	export default {
		data() {
			return {
				editVisible:false,
				grouplist:[],
				categoryTypeList:[{'category_name':'所有','id':0}],
				warehouseList:[{'warehouse_name':'所有','id':0}],
				id:'',
				warehouse_category_id:'',
				userform:{
					user_group_id: null,
					user_passwd:"",
					target_user_name: "",
					warehouse_category_id:0,
					warehouse_name_id:0,
				},
				rules:{
					target_user_name:[
						{ required: true, message: this.$t('user.userholder'), trigger: 'blur' },
						{
			        validator: (rule, value, callback) => {
			          if (/[\u4e00-\u9fa5_a-zA-Z0-9_]{3,16}/.test(value)==false) {
			            callback(new Error(this.$t('user.usertips')));
			          }else{
			          	callback();
			          }
			        }, trigger: 'blur'
				    },
					],
					// user_passwd:[{ required: true, message: this.$t('user.passwordholder'), trigger: 'blur' }],
					user_group_id:[{ required: true, message: this.$t('user.authorizationholder'), trigger: 'change' }],
				}				
			}
		},
		mounted(){
			 this.userform.user_passwd = ''
			 this.userform.target_user_name = ''
		},
		methods: {
			 //获取权限组列表
    	getUserGroupList(){
	      userGroupList({}).then(res =>{
          if(res.result){
            this.grouplist = res.data
          }else{
            this.$message({
              showClose: true,
              message: res.msg,
              type: 'error'
            })
          }
	      })
	    },
	    // 获取库位类型
	    getCategoryType(){
	    	searchWarehouseCategory({}).then(res =>{
	    		if (res.result) {
	    			this.categoryTypeList = this.categoryTypeList.concat(res.data_list)
	    		}
	    	})
	    },
	    // 获取库位
	    getWarehouseList(){
	    	this.warehouseList = [{'warehouse_name':'所有','id':0}]
	    	this.userform.warehouse_name_id = 0
	    	searchWarehouseNameByCategory({warehouse_category_id:this.userform.warehouse_category_id}).then(res =>{
	    		if (res.result) {
	    			this.warehouseList = this.warehouselist.concat(res.data)
	    		}
	    	})
	    },
	    // 获取库位
	    categoryTypeChange(){
	    	this.userform.warehouse_name_id = 0
	    	this.getWarehouseList()
	    },
			userDetail(userInfo){
				this.editVisible = true
				this.id = userInfo.user_id
				this.warehouse_category_id = userInfo.warehouse_category_id
				this.userform.target_user_name = userInfo.user_name
				this.userform.user_group_id = userInfo.user_group_id
				this.userform.warehouse_category_id = userInfo.warehouse_category_id
				this.userform.warehouse_name_id = userInfo.warehouse_name_id
				this.getUserGroupList(); 
				this.getCategoryType();
				this.categoryTypeChange();
			},
			reset(userform){
       this.$refs.userform.resetFields();
    	},
			// 保存用户
			userSave(userform) {
				this.$refs.userform.validate((valid) => {
          if (valid) {
          		let data={
          			'warehouse_category_id':this.warehouse_category_id,
          			'target_user_id':this.user_id,
          			'target_user_name':this.userform.target_user_name,
          			'target_user_new_passwd':this.userform.user_passwd,
          			'target_user_group_id':this.userform.user_group_id,
          			'target_user_warehouse_category_id':this.userform.warehouse_category_id,
          			'target_user_warehouse_name_id':this.userform.warehouse_name_id
          		}
            adminChangeUserInfo(data).then(res =>{
            	if (res.result) {
            		this.editVisible = false;
            		this.$emit('getUserList')
            	} else {
            		this.$message({
		              showClose: true,
		              message: res.msg,
		              type: 'error'
		            })
            	}
            })
          } else {
            return false;
          }
        });
			},
			// 取消保存
			userCancel(userform) {
				 this.$refs.userform.resetFields();
				 this.editVisible = false;
			}
		}
	}
</script>
<style>
.add_user_form{
	text-align: left;
}
.add_user_form .el-input__inner{
	width:220px;
}
.form_item_left{
	text-align: left!important;
}
/*.add_user_form {
	margin:0 20px;
}*/
	.small_form .el-form-item__label{
	text-align: left;
	}
	.small_form .el-form-item__content{
		width:200px;
	}
	.small_form .el-input .el-input__inner{
		width:200px;
	}
</style>


